import { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

// chromeTypeScript
declare const chrome: any;

export default function Sign(): JSX.Element {
  const location = useLocation();
  const [origin, setOrigin] = useState<string>("");
  const [message, setMessage] = useState<string>("");
  const [requestId, setRequestId] = useState<string>("");
  
  useEffect(() => {
    // URLID
    const searchParams = new URLSearchParams(location.search);
    const originParam = searchParams.get("origin");
    const messageParam = searchParams.get("message");
    const requestIdParam = searchParams.get("requestId");
    
    if (originParam) {
      setOrigin(decodeURIComponent(originParam));
    }
    
    if (messageParam) {
      setMessage(decodeURIComponent(messageParam));
    }
    
    if (requestIdParam) {
      setRequestId(requestIdParam);
    }
  }, [location]);
  
  const handleApprove = () => {
    // 
    const signature = `0x${Array.from(crypto.getRandomValues(new Uint8Array(65)))
      .map((b: number) => b.toString(16).padStart(2, '0'))
      .join('')}`;
      
    // background.js
    chrome.runtime.sendMessage({
      type: 'signature_response',
      requestId,
      approved: true,
      signature,
      origin
    }, (response: any) => {
      console.log(':', response);
    });
    
    // background.js
  };
  
  const handleReject = () => {
    // background.js
    chrome.runtime.sendMessage({
      type: 'signature_response',
      requestId,
      approved: false,
      origin
    }, (response: any) => {
      console.log(':', response);
    });
    
    // background.js
  };
  
  return (
    <div style={{ padding: "20px", color: "white" }}>
      <h2></h2>
      <p>{origin} :</p>
      
      <div style={{ 
        marginTop: "20px", 
        background: "#2c2c2c", 
        padding: "15px", 
        borderRadius: "4px",
        wordBreak: "break-word"
      }}>
        <p>{message}</p>
      </div>
      
      <div style={{ display: "flex", justifyContent: "space-between", marginTop: "30px" }}>
        <button
          onClick={handleReject}
          style={{
            padding: "10px 20px",
            background: "#555",
            border: "none",
            borderRadius: "4px",
            color: "white",
            cursor: "pointer"
          }}
        >
          
        </button>
        <button
          onClick={handleApprove}
          style={{
            padding: "10px 20px",
            background: "#3498db",
            border: "none",
            borderRadius: "4px",
            color: "white",
            cursor: "pointer"
          }}
        >
          
        </button>
      </div>
    </div>
  );
} 