import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  extensionApi,
  IpfsFile,
  IpfsNode,
} from "../utils/chromeApi";
import { Account, accountStorage } from "../chromeStore/accountStorage.ts";
import AccountHeader from "./AccountHeader";
import IpfsFileList from "./IpfsFileList";

const Home: React.FC = () => {
  const navigate = useNavigate();
  const [currentIpfsNode, setCurrentIpfsNode] = useState<IpfsNode | null>(null);
  const [connectedSites, setConnectedSites] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [accounts, setAccounts] = useState<Account[]>([]);
  const [currentAccount, setCurrentAccount] = useState<Account | null>(null);
  const [showFileHistory, setShowFileHistory] = useState<boolean>(true);

  useEffect(() => {
    const loadData = async () => {
      try {
        setIsLoading(true);

        // localStoragelocalForage
        await accountStorage.migrateFromLocalStorage();

        // IPFS
        const node = await extensionApi.getCurrentIpfsNode();
        setCurrentIpfsNode(node);

        // 
        const sites = await extensionApi.getConnectedSites();
        setConnectedSites(sites);

        // 
        const allAccounts = await accountStorage.getAccounts();
        setAccounts(allAccounts);

        // 
        if (!allAccounts || allAccounts.length === 0) {
          navigate("/create-password");
          return;
        }

        // 
        const account = await accountStorage.getCurrentAccount();
        setCurrentAccount(account);
      } catch (error) {
        console.error(":", error);
        navigate("/create-account");
      } finally {
        setIsLoading(false);
      }
    };

    loadData();
  }, [navigate]);

  // 
  const handleAccountChange = async (accountId: string) => {
    try {
      await accountStorage.setCurrentAccountId(accountId);
      const newCurrentAccount = accounts.find((acc) => acc.id === accountId);
      if (newCurrentAccount) {
        setCurrentAccount(newCurrentAccount);
      }
    } catch (error) {
      console.error(":", error);
    }
  };
  
  // 
  const handleAccountDelete = async (accountId: string): Promise<boolean> => {
    try {
      // accountStorage
      const success = await accountStorage.deleteAccount(accountId);
      
      if (success) {
        // 
        const updatedAccounts = await accountStorage.getAccounts();
        setAccounts(updatedAccounts);
        
        // 
        const updatedCurrentAccount = await accountStorage.getCurrentAccount();
        setCurrentAccount(updatedCurrentAccount);
      }
      
      return success;
    } catch (error) {
      console.error(":", error);
      return false;
    }
  };

  // 
  const handleFileSelect = (file: IpfsFile) => {
    navigator.clipboard.writeText(file.cid);
    alert(`CID: ${file.cid}`);
  };

  // 
  const toggleFileHistory = () => {
    setShowFileHistory(!showFileHistory);
  };

  // 
  if (isLoading || !currentAccount) {
    return (
      <div
        style={{
          backgroundColor: "#1c1c1c",
          color: "white",
          padding: "40px",
          borderRadius: "16px",
          maxWidth: "400px",
          margin: "0 auto",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "300px",
        }}
      >
        ...
      </div>
    );
  }

  return (
    <div
      style={{
        backgroundColor: "#1c1c1c",
        color: "white",
        padding: "20px",
        borderRadius: "16px",
        maxWidth: "400px",
        margin: "0 auto",
        boxShadow: "0 4px 16px rgba(0, 0, 0, 0.3)",
      }}
    >
      {/*  */}
      <AccountHeader
        currentAccount={currentAccount}
        accounts={accounts}
        onAccountChange={handleAccountChange}
        onAccountDelete={handleAccountDelete}
      />

      <h1 style={{ textAlign: "center", marginBottom: "20px" }}></h1>

      {/* IPFS */}
      <div
        style={{
          backgroundColor: "#2a2a2a",
          borderRadius: "12px",
          padding: "20px",
          marginBottom: "20px",
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            marginBottom: "15px",
          }}
        >
          <h3 style={{ fontSize: "18px" }}></h3>
          <div style={{ display: "flex" }}>
            <button
              onClick={() => navigate("/ipfs-settings")}
              style={{
                padding: "5px 10px",
                backgroundColor: "#2ecc71",
                color: "white",
                border: "none",
                borderRadius: "4px",
                fontSize: "12px",
                cursor: "pointer",
                marginRight: "5px",
              }}
            >
              
            </button>
          </div>
        </div>

        {isLoading
          ? (
            <div style={{ padding: "10px", textAlign: "center" }}>
              ...
            </div>
          )
          : (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                padding: "10px",
                background: "#333",
                borderRadius: "8px",
              }}
            >
              <div
                style={{
                  width: "40px",
                  height: "40px",
                  borderRadius: "50%",
                  backgroundColor: "#2ecc71",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  marginRight: "15px",
                  fontSize: "12px",
                }}
              >
                IPFS
              </div>
              <div>
                <p style={{ fontWeight: "bold" }}>
                  {currentIpfsNode?.name || ""}
                </p>
                <p style={{ fontSize: "12px", color: "#999" }}>
                  {currentIpfsNode?.url || "URL"}
                </p>
              </div>
            </div>
          )}
      </div>

      {/* IPFS */}
      <div
        style={{
          backgroundColor: "#2a2a2a",
          borderRadius: "12px",
          padding: "20px",
          marginBottom: "20px",
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            cursor: "pointer",
          }}
          onClick={toggleFileHistory}
        >
          <h3 style={{ fontSize: "18px", margin: 0 }}></h3>
          <button
            onClick={() =>
              navigate("/ipfs-settings", { state: { tab: "files" } })}
            style={{
              padding: "5px 10px",
              backgroundColor: "#3b99fc",
              color: "white",
              border: "none",
              borderRadius: "4px",
              fontSize: "12px",
              cursor: "pointer",
            }}
          >
            
          </button>
        </div>

        {showFileHistory && (
          <div style={{ marginTop: "15px" }}>
            <IpfsFileList onFileSelect={handleFileSelect} />
          </div>
        )}
      </div>

      {/*  */}
      <div
        style={{
          backgroundColor: "#2a2a2a",
          borderRadius: "12px",
          padding: "20px",
          marginBottom: "20px",
        }}
      >
        <h3 style={{ fontSize: "18px", marginBottom: "15px" }}></h3>

        {isLoading
          ? (
            <div style={{ padding: "10px", textAlign: "center" }}>
              ...
            </div>
          )
          : connectedSites.length === 0
          ? (
            <div
              style={{ padding: "10px", textAlign: "center", color: "#999" }}
            >
              
            </div>
          )
          : (
            <div style={{ maxHeight: "150px", overflowY: "auto" }}>
              {connectedSites.map((site, index) => (
                <div
                  key={index}
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    padding: "10px",
                    borderBottom: index < connectedSites.length - 1
                      ? "1px solid #444"
                      : "none",
                    background: "#333",
                    borderRadius: "4px",
                    marginBottom: "8px",
                  }}
                >
                  <div style={{ color: "#fff" }}>
                    {site}
                  </div>
                  <button
                    style={{
                      backgroundColor: "#e74c3c",
                      color: "white",
                      border: "none",
                      borderRadius: "4px",
                      padding: "4px 8px",
                      fontSize: "12px",
                      cursor: "pointer",
                    }}
                  >
                    
                  </button>
                </div>
              ))}
            </div>
          )}
      </div>
    </div>
  );
};

export default Home;
