import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { accountStorage } from '../chromeStore/accountStorage.ts';

const CreateAccount: React.FC = () => {
  const [accountName, setAccountName] = useState('');
  const [isCreating, setIsCreating] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  // 
  useEffect(() => {
    const checkForAccounts = async () => {
      try {
        // localStoragelocalForage
        await accountStorage.migrateFromLocalStorage();
        
        // 
        const accounts = await accountStorage.getAccounts();
        
        if (accounts.length === 0) {
          // 
          setAccountName('');
          setIsCreating(true);
          
          // 
          await accountStorage.initializeIfNeeded();
          
          // 
          setTimeout(() => {
            navigate('/home');
          }, 1000);
        }
      } catch (error) {
        console.error(':', error);
        setError('');
        setIsCreating(false);
      }
    };

    checkForAccounts();
  }, [navigate]);

  const handleCreateAccount = async () => {
    if (!accountName.trim()) {
      setError('');
      return;
    }

    try {
      setIsCreating(true);
      setError('');

      // accountStorage API
      const newAccount = await accountStorage.addAccount(accountName);
      
      if (!newAccount) {
        throw new Error('');
      }

      // 
      navigate('/home');
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setIsCreating(false);
    }
  };

  return (
    <div style={{
      backgroundColor: '#1c1c1c',
      color: 'white',
      padding: '40px',
      borderRadius: '16px',
      maxWidth: '400px',
      margin: '0 auto',
      boxShadow: '0 4px 16px rgba(0, 0, 0, 0.3)'
    }}>
      <h1 style={{ 
        textAlign: 'center', 
        marginBottom: '30px',
        fontSize: '24px'
      }}>
        
      </h1>

      {error && (
        <div style={{
          backgroundColor: '#e74c3c',
          color: 'white',
          padding: '10px',
          borderRadius: '4px',
          marginBottom: '20px',
          textAlign: 'center'
        }}>
          {error}
        </div>
      )}

      <div style={{ marginBottom: '30px' }}>
        <label style={{ 
          display: 'block', 
          marginBottom: '10px',
          fontSize: '14px'
        }}>
          
        </label>
        <input
          type="text"
          value={accountName}
          onChange={(e) => setAccountName(e.target.value)}
          placeholder=""
          style={{
            width: '100%',
            padding: '12px',
            backgroundColor: '#2a2a2a',
            border: 'none',
            borderRadius: '4px',
            color: 'white',
            fontSize: '16px'
          }}
        />
      </div>

      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between',
        gap: '15px'
      }}>
        <button
          onClick={() => navigate('/home')}
          disabled={isCreating}
          style={{
            flex: 1,
            padding: '14px',
            backgroundColor: '#555',
            color: 'white',
            border: 'none',
            borderRadius: '50px',
            fontSize: '16px',
            cursor: isCreating ? 'not-allowed' : 'pointer',
            opacity: isCreating ? 0.7 : 1
          }}
        >
          
        </button>
        <button
          onClick={handleCreateAccount}
          disabled={isCreating}
          style={{
            flex: 1,
            padding: '14px',
            backgroundColor: '#3b99fc',
            color: 'white',
            border: 'none',
            borderRadius: '50px',
            fontSize: '16px',
            fontWeight: 'bold',
            cursor: isCreating ? 'not-allowed' : 'pointer',
            opacity: isCreating ? 0.7 : 1
          }}
        >
          {isCreating ? '...' : ''}
        </button>
      </div>
    </div>
  );
};

export default CreateAccount; 