import React, { useState, useEffect } from 'react';
import { IpfsFile } from '../utils/chromeApi';
import localforage from 'localforage';

// localforage
const ipfsFilesStore = localforage.createInstance({
  name: 'ipfs-files',
  storeName: 'files'
});

interface IpfsFileListProps {
  onFileSelect?: (file: IpfsFile) => void;
}

const IpfsFileList: React.FC<IpfsFileListProps> = ({ onFileSelect }) => {
  const [files, setFiles] = useState<IpfsFile[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // 
  const formatFileSize = (bytes: number): string => {
    if (bytes < 1024) return bytes + ' B';
    if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(2) + ' KB';
    if (bytes < 1024 * 1024 * 1024) return (bytes / (1024 * 1024)).toFixed(2) + ' MB';
    return (bytes / (1024 * 1024 * 1024)).toFixed(2) + ' GB';
  };

  // 
  const formatDate = (timestamp: number): string => {
    const date = new Date(timestamp);
    return date.toLocaleString();
  };
  
  // 
  const truncateFileName = (fileName: string, maxLength: number = 20): string => {
    if (fileName.length <= maxLength) return fileName;
    
    // 
    const lastDot = fileName.lastIndexOf('.');
    const ext = lastDot !== -1 ? fileName.slice(lastDot) : '';
    
    // 
    const prefixLength = maxLength - 3 - ext.length; // 3"..."
    
    if (prefixLength <= 0) {
      // 
      return fileName.slice(0, 3) + '...' + ext.slice(0, maxLength - 6) + '...';
    }
    
    return fileName.slice(0, prefixLength) + '...' + ext;
  };

  // Blob URL
  const createAndDownloadFile = (content: ArrayBuffer, fileName: string, mimeType: string) => {
    const blob = new Blob([content], { type: mimeType });
    const url = URL.createObjectURL(blob);
    
    const a = document.createElement('a');
    a.href = url;
    a.download = fileName;
    document.body.appendChild(a);
    a.click();
    
    // 
    setTimeout(() => {
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    }, 100);
  };

  // 
  const handleGetFile = async (file: IpfsFile) => {
    try {
      setIsLoading(true);
      setError(null);
      
      // localForage
      const fileData = await ipfsFilesStore.getItem<{ content: ArrayBuffer, type: string }>(`file:${file.cid}`);
      
      if (!fileData) {
        throw new Error('');
      }
      
      // 
      createAndDownloadFile(
        fileData.content, 
        file.name, 
        fileData.type || 'application/octet-stream'
      );
      
    } catch (err) {
      console.error(':', err);
      setError((err as Error).message || '');
      alert(`: ${(err as Error).message}`);
    } finally {
      setIsLoading(false);
    }
  };

  // 
  const loadFiles = async () => {
    try {
      setIsLoading(true);
      setError(null);
      
      // localForage
      const uploadHistory = await ipfsFilesStore.getItem<IpfsFile[]>('uploadHistory');
      setFiles(uploadHistory || []);
    } catch (err) {
      console.error(':', err);
      setError((err as Error).message || '');
    } finally {
      setIsLoading(false);
    }
  };

  // 
  const handleClearHistory = async () => {
    if (window.confirm('')) {
      try {
        setIsLoading(true);
        setError(null);
        
        // CID
        const history = await ipfsFilesStore.getItem<IpfsFile[]>('uploadHistory') || [];
        
        // 
        for (const file of history) {
          await ipfsFilesStore.removeItem(`file:${file.cid}`);
        }
        
        // 
        await ipfsFilesStore.setItem('uploadHistory', []);
        setFiles([]);
      } catch (err) {
        console.error(':', err);
        setError((err as Error).message || '');
      } finally {
        setIsLoading(false);
      }
    }
  };

  // 
  useEffect(() => {
    loadFiles();
  }, []);

  return (
    <div style={{ width: '100%' }}>
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        marginBottom: '16px'
      }}>
        <h3 style={{ margin: 0, color: 'white' }}></h3>
        {files.length > 0 && (
          <button
            onClick={handleClearHistory}
            style={{
              background: 'none',
              border: 'none',
              color: '#ff5c5c',
              cursor: 'pointer',
              fontSize: '14px'
            }}
          >
            
          </button>
        )}
      </div>

      {isLoading && (
        <div style={{ textAlign: 'center', padding: '20px', color: '#aaa' }}>
          ...
        </div>
      )}

      {error && (
        <div style={{
          color: '#ff5c5c',
          padding: '8px',
          marginTop: '10px',
          fontSize: '14px'
        }}>
          {error}
        </div>
      )}

      {!isLoading && files.length === 0 && !error && (
        <div style={{ textAlign: 'center', padding: '20px', color: '#aaa' }}>
          
        </div>
      )}

      {files.length > 0 && (
        <div style={{ maxHeight: '300px', overflowY: 'auto' }}>
          {files.map((file, index) => (
            <div
              key={file.cid}
              style={{
                padding: '12px',
                backgroundColor: index % 2 === 0 ? '#2a2a2a' : '#333',
                borderRadius: '6px',
                marginBottom: '8px',
                cursor: 'pointer',
                transition: 'background-color 0.2s',
                position: 'relative',
              }}
              onClick={() => onFileSelect && onFileSelect(file)}
            >
              <div style={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center'
              }}>
                <div 
                  style={{ 
                    color: 'white', 
                    fontWeight: 500,
                    maxWidth: '70%', 
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                  title={file.name} // 
                >
                  {truncateFileName(file.name, 25)}
                </div>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleGetFile(file);
                  }}
                  style={{
                    background: '#3b99fc',
                    border: 'none',
                    color: 'white',
                    padding: '4px 8px',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    fontSize: '12px'
                  }}
                >
                  
                </button>
              </div>
              
              <div style={{ 
                fontSize: '12px', 
                color: '#aaa', 
                marginTop: '4px',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap'
              }}
              title={`CID: ${file.cid}`}>
                CID: {file.cid}
              </div>
              
              <div style={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                fontSize: '12px',
                color: '#888',
                marginTop: '4px'
              }}>
                <div>{formatFileSize(file.size)}</div>
                <div>{formatDate(file.timestamp)}</div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default IpfsFileList; 