import { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { ipfsNodeApi, IpfsNode, DEFAULT_IPFS_NODES, IpfsFile } from "../utils/chromeApi";
import IpfsFileUpload from "./IpfsFileUpload";
import IpfsFileList from "./IpfsFileList";

interface LocationState {
  tab?: 'nodes' | 'files';
}

export default function IpfsSettings(): JSX.Element {
  const navigate = useNavigate();
  const location = useLocation();
  const state = location.state as LocationState;
  
  const [ipfsNodes, setIpfsNodes] = useState<IpfsNode[]>(DEFAULT_IPFS_NODES);
  const [selectedNodeId, setSelectedNodeId] = useState<string>("1");
  const [newNodeName, setNewNodeName] = useState<string>("");
  const [newNodeUrl, setNewNodeUrl] = useState<string>("");
  const [testResult, setTestResult] = useState<string>("");
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [errorMessage, setErrorMessage] = useState<string>("");
  const [activeTab, setActiveTab] = useState<'nodes' | 'files'>(state?.tab || 'nodes');

  // IPFS
  useEffect(() => {
    const loadData = async () => {
      try {
        setIsLoading(true);
        setErrorMessage("");
        
        // 
        const nodes = await ipfsNodeApi.getNodes();
        setIpfsNodes(nodes);
        
        // ID
        const nodeId = await ipfsNodeApi.getSelectedNodeId();
        setSelectedNodeId(nodeId);
      } catch (error) {
        console.error("IPFS:", error);
        setErrorMessage("IPFS");
      } finally {
        setIsLoading(false);
      }
    };
    
    loadData();
  }, []);

  // 
  const saveNodes = async (nodes: IpfsNode[], selectedId: string) => {
    try {
      setErrorMessage("");
      await ipfsNodeApi.saveNodes(nodes);
      await ipfsNodeApi.setSelectedNodeId(selectedId);
    } catch (error) {
      console.error(":", error);
      setErrorMessage("");
    }
  };

  // 
  const handleAddNode = () => {
    if (!newNodeName || !newNodeUrl) {
      setTestResult("URL");
      return;
    }

    const newNode: IpfsNode = {
      id: Date.now().toString(),
      name: newNodeName,
      url: newNodeUrl
    };

    const updatedNodes = [...ipfsNodes, newNode];
    setIpfsNodes(updatedNodes);
    saveNodes(updatedNodes, selectedNodeId);
    setNewNodeName("");
    setNewNodeUrl("");
    setTestResult("");
  };

  // 
  const handleSelectNode = (id: string) => {
    setSelectedNodeId(id);
    saveNodes(ipfsNodes, id);
    setTestResult("");
  };

  // 
  const handleDeleteNode = (id: string) => {
    // 
    if (ipfsNodes.length <= 1) {
      setTestResult("");
      return;
    }

    const updatedNodes = ipfsNodes.filter(node => node.id !== id);
    setIpfsNodes(updatedNodes);
    
    // 
    if (id === selectedNodeId) {
      setSelectedNodeId(updatedNodes[0].id);
      saveNodes(updatedNodes, updatedNodes[0].id);
    } else {
      saveNodes(updatedNodes, selectedNodeId);
    }
    
    setTestResult("");
  };

  // 
  const testConnection = async () => {
    setIsLoading(true);
    setTestResult("...");
    setErrorMessage("");
    
    try {
      const result = await ipfsNodeApi.testConnection(selectedNodeId);
      setTestResult(result.success ? 
        ` ${ipfsNodes.find(node => node.id === selectedNodeId)?.name}` : 
        `: ${result.message}`
      );
    } catch (error) {
      setTestResult(`: ${error instanceof Error ? error.message : ''}`);
    } finally {
      setIsLoading(false);
    }
  };

  // 
  const handleFileUploaded = (file: IpfsFile) => {
    // 
    setTestResult(` "${file.name}" CID: ${file.cid}`);
  };

  // 
  const handleFileSelect = (file: IpfsFile) => {
    navigator.clipboard.writeText(file.cid);
    setTestResult(` CID: ${file.cid}`);
  };

  return (
    <div style={{ padding: "20px", color: "white", maxWidth: "100%" }}>
      <h2 style={{ textAlign: "center" }}></h2>
      
      {errorMessage && (
        <div style={{ 
          background: "#e74c3c", 
          padding: "10px", 
          borderRadius: "4px",
          marginBottom: "15px",
          color: "white" 
        }}>
          : {errorMessage}
        </div>
      )}

      {/*  */}
      <div style={{ 
        display: 'flex', 
        marginBottom: '20px', 
        borderBottom: '1px solid #444',
      }}>
        <div 
          onClick={() => setActiveTab('nodes')}
          style={{ 
            padding: '10px 15px', 
            cursor: 'pointer',
            borderBottom: activeTab === 'nodes' ? '2px solid #3498db' : 'none',
            color: activeTab === 'nodes' ? '#fff' : '#aaa',
            fontWeight: activeTab === 'nodes' ? 'bold' : 'normal',
            marginRight: '10px'
          }}
        >
          
        </div>
        <div 
          onClick={() => setActiveTab('files')}
          style={{ 
            padding: '10px 15px', 
            cursor: 'pointer',
            borderBottom: activeTab === 'files' ? '2px solid #3498db' : 'none',
            color: activeTab === 'files' ? '#fff' : '#aaa',
            fontWeight: activeTab === 'files' ? 'bold' : 'normal'
          }}
        >
          
        </div>
      </div>

      {testResult && (
        <div style={{ 
          marginBottom: "15px",
          background: testResult.includes('') ? "#3a1c17" : "#1a321b",
          padding: "10px", 
          borderRadius: "4px",
          color: testResult.includes('') ? "#e74c3c" : "#2ecc71" 
        }}>
          {testResult}
        </div>
      )}
      
      {activeTab === 'nodes' ? (
        /*  */
        <>
          <div style={{ marginBottom: "20px" }}>
            <h3></h3>
            {isLoading ? (
              <div style={{ padding: "10px", textAlign: "center" }}>...</div>
            ) : (
              <div style={{ 
                background: "#2c2c2c", 
                padding: "10px", 
                borderRadius: "4px",
                marginBottom: "10px" 
              }}>
                {ipfsNodes.find(node => node.id === selectedNodeId)?.name || ""}
              </div>
            )}
            <button 
              onClick={testConnection}
              disabled={isLoading}
              style={{
                padding: "5px 10px",
                background: "#3498db",
                border: "none",
                borderRadius: "4px",
                color: "white",
                cursor: isLoading ? "not-allowed" : "pointer",
                opacity: isLoading ? 0.7 : 1
              }}
            >
              {isLoading ? "..." : ""}
            </button>
          </div>
          
          <div style={{ marginBottom: "20px" }}>
            <h3></h3>
            <div style={{ 
              overflowY: "auto",
              background: "#2c2c2c", 
              padding: "10px", 
              borderRadius: "4px" 
            }}>
              {isLoading ? (
                <div style={{ padding: "10px", textAlign: "center" }}>...</div>
              ) : ipfsNodes.length === 0 ? (
                <div style={{ padding: "10px", textAlign: "center" }}></div>
              ) : (
                ipfsNodes.map(node => (
                  <div 
                    key={node.id}
                    style={{ 
                      padding: "8px", 
                      marginBottom: "5px", 
                      background: node.id === selectedNodeId ? "#3498db" : "#444",
                      borderRadius: "4px",
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center"
                    }}
                  >
                    <div>
                      <div style={{ fontWeight: "bold" }}>{node.name}</div>
                      <div style={{ fontSize: "12px", opacity: 0.8 }}>{node.url}</div>
                    </div>
                    <div>
                      {node.id !== selectedNodeId && (
                        <button
                          onClick={() => handleSelectNode(node.id)}
                          style={{
                            padding: "5px 10px",
                            background: "#2ecc71",
                            border: "none",
                            borderRadius: "4px",
                            color: "white",
                            cursor: "pointer",
                            marginRight: "5px"
                          }}
                        >
                          
                        </button>
                      )}
                      <button
                        onClick={() => handleDeleteNode(node.id)}
                        style={{
                          padding: "5px 10px",
                          background: "#e74c3c",
                          border: "none",
                          borderRadius: "4px",
                          color: "white",
                          cursor: "pointer"
                        }}
                      >
                        
                      </button>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
          
          <div style={{ marginBottom: "20px" }}>
            <h3></h3>
            <div style={{ marginBottom: "10px" }}>
              <input
                type="text"
                placeholder=""
                value={newNodeName}
                onChange={(e) => setNewNodeName(e.target.value)}
                style={{
                  width: "100%",
                  padding: "8px",
                  marginBottom: "5px",
                  background: "#333",
                  border: "none",
                  borderRadius: "4px",
                  color: "white"
                }}
              />
            </div>
            <div style={{ marginBottom: "10px" }}>
              <input
                type="text"
                placeholder="URL"
                value={newNodeUrl}
                onChange={(e) => setNewNodeUrl(e.target.value)}
                style={{
                  width: "100%",
                  padding: "8px",
                  marginBottom: "10px",
                  background: "#333",
                  border: "none",
                  borderRadius: "4px",
                  color: "white"
                }}
              />
            </div>
            <button
              onClick={handleAddNode}
              style={{
                padding: "8px 16px",
                background: "#2ecc71",
                border: "none",
                borderRadius: "4px",
                color: "white",
                cursor: "pointer"
              }}
            >
              
            </button>
          </div>
        </>
      ) : (
        /*  */
        <>
          <div style={{ marginBottom: "20px" }}>
            <h3></h3>
            <IpfsFileUpload onFileUploaded={handleFileUploaded} />
          </div>
          
          <IpfsFileList onFileSelect={handleFileSelect} />
        </>
      )}
      
      <button
        onClick={() => navigate(-1)}
        style={{
          display: "block",
          padding: "8px 16px",
          background: "#3498db",
          border: "none",
          borderRadius: "4px",
          color: "white",
          cursor: "pointer",
          marginTop: "20px"
        }}
      >
        
      </button>
    </div>
  );
} 